.cases{
    width: 100%;
     /* height:150vh;
     height: calc(var(--vh,1vh)*150); */
     background-color: $white;
     margin-top: 20%;
     margin-bottom: 10%;
     @include media('<=phone'){
        margin-top: 50%;
        }
    .row{
        display: block !important;
        .case{
        background-color: $white;
        //  position: relative;
        //  background: $black;
        //  cursor: pointer;
        /* margin-top: 2% !important;
        margin-bottom: 0 !important;
        padding-top: 5% !important;
        padding-bottom: 5% !important; */
                
        .case-details{
            width: 100%;
            //height: 100%;
            flex-direction: column;
            justify-content: center;
            // height: calc(var(--vh,1vh)*50);
            //    box-sizing: border-box;
            //    z-index: 1;
            //   position: relative;

            .norow{
                display: flex !important;
                @include media('<=phone'){
                    display:block !important;
                    flex-direction: column !important;
             }
            }

            .der{
                width: 80% !important;
                margin: 0 auto;
                display: flex;
                margin-bottom: 2%;
                //justify-content: space-evenly;
                @include media('<=phone'){
                    display:block !important;
             }
            }

            span{
                width: 100%;    
                margin: 0 auto;
                //   margin-top: 156px;
                font-size: 5vh;
                font-weight: 55;
                color: $black;
                text-transform: uppercase;
                @include media('<=tablet'){
                    font-size: 3vh;
             }
                @include media('<=phone'){
                    font-size: 3vh;
             }
            }
            
            .der{
               
                    @include media('<=phone'){
                        margin: 0 auto;
                        text-align: center;
                        
                        width: 60% !important;
                    }
            }

                .sub{
                            
                    margin: 0 auto; 
                    font-family: "EditorialNewUltralightItalic", "Helvetica Neue", sans-serif;
                     -webkit-font-smoothing: antialiased;
                     -moz-osx-font-smoothing: grayscale;
                     letter-spacing: 3%;          
                    //text-transform: uppercase;
                    font-size: 3vh;
                    font-weight: 400;
                    color: $black;
                    
                 }

                

                .des{
                    margin: 0 auto;
                    text-align: center;
                    font-size: 4.3vh;
                    width: 60% !important;
                    @include media('<=tablet'){
                        width: 100% !important;
                        font-size: 3vh;
                    }
                    @include media('<=phone'){
                        width: 100% !important;
                        font-size: 3vh;
                    }
                    
                } 

                
                .part{
                    //width: 100%;
                    margin: 0 auto;
                    display: block;
                    
                }
                h2{
                    font-family: "EditorialNewUltralightItalic", "Helvetica Neue", sans-serif;
                     -webkit-font-smoothing: antialiased;
                     -moz-osx-font-smoothing: grayscale;
                    text-align: center;
                    font-size: 15vh;
                    //line-height: 12vh;
                    letter-spacing: -0.3rem !important;
                    font-weight: 200 !important;
                    text-transform: capitalize;
                    font-style: italic;
                    

                    // width: 85%;
                    // margin-top: 16px;
                    color: $black;
                    //margin-bottom: 5% !important; 
                    @include media('<=tablet'){
                        font-size: 6vh;
                        letter-spacing: -0.1rem !important;
                    }
                    @include media('<=phone'){
                        font-size: 6vh;
                        letter-spacing: -0.1rem !important;
                    }
                }
            }
            .case-image{
                top: 0;
                left: 0;
                transition: .4s cubic-bezier(.6,-.05,.1,.99);
                img{
                //  height: 50%;
                    width: 100%;
                    margin-top: 2%;

                    //  object-fit: cover;
                }

                .half-left{
                   
                    width: 50%;
                    right: 0;
                    @include media('<=phone'){
                           width: 100%;
                    }
                }
            }

        }

    }
}